.footer {
  background-color: #2B2B2B;
  color: white;
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
}
.footer__body {
  min-height: 388px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding-left: 107px;
  padding-right: 107px;
}
@media (max-width: 1440px) {
  .footer__body {
    min-height: calc(250px + 138 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .footer__body {
    padding-left: calc(0px + 107 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .footer__body {
    padding-right: calc(0px + 107 * ((100vw - 320px) / 1120));
  }
}
.communication-footer {
  display: flex;
  padding: 5px;
  min-height: 300px;
}
@media (max-width: 1440px) {
  .communication-footer {
    min-height: calc(162px + 138 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 767.98px) {
  .communication-footer {
    flex-direction: column;
    gap: 30px;
  }
}
@media (max-width: 767.98px) and (max-width: 1440px) {
  .communication-footer {
    gap: calc(20px + 10 * ((100vw - 320px) / 1120));
  }
}
.contact-footer {
  flex: 1 1 38.1944444444%;
  border-right: white 1px solid;
  display: flex;
  align-items: center;
}
@media (max-width: 767.98px) {
  .contact-footer {
    border: 0;
    justify-content: center;
  }
}
.contact-footer__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 33px;
}
@media (max-width: 1440px) {
  .contact-footer__list {
    gap: calc(15px + 18 * ((100vw - 320px) / 1120));
  }
}
.contact-footer__item {
  min-width: 250px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.contact-footer__img {
  width: 50px;
  height: 50px;
}
.contact-footer__text {
  font-size: 24px;
}
@media (max-width: 1440px) {
  .contact-footer__text {
    font-size: calc(20px + 4 * ((100vw - 320px) / 1120));
  }
}

.social-network {
  flex: 1 1 38.1944444444%;
  border-left: white 1px solid;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2px;
}
@media (max-width: 1440px) {
  .social-network {
    gap: calc(0px + 2 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 767.98px) {
  .social-network {
    border: 0;
    justify-content: center;
  }
}
.social-network__img {
  width: 60px;
  height: 60px;
}

.rights-footer {
  margin: 0 auto;
  max-width: 300px;
  display: flex;
  gap: 13px;
  align-items: center;
  padding-bottom: 5px;
}
@media (max-width: 1440px) {
  .rights-footer {
    gap: calc(10px + 3 * ((100vw - 320px) / 1120));
  }
}
.rights-footer__img {
  width: 20px;
  height: 20px;
}
.rights-footer__text {
  font-size: 16px;
}
@media (max-width: 1440px) {
  .rights-footer__text {
    font-size: calc(12px + 4 * ((100vw - 320px) / 1120));
  }
}

