.header {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  margin: 0 auto;
  max-width: 1440px;
  color: white;
  background-color: #2B2B2B;
  box-shadow: 0 7px 5px rgba(0, 0, 0, 0.2);
}
.header__container {
  display: flex;
  align-items: center;
}
.header__body {
  min-height: 100px;
  padding-left: 43px;
  padding-right: 43px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1 1 91.9444444444%;
}
@media (max-width: 1440px) {
  .header__body {
    padding-left: calc(0px + 43 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .header__body {
    padding-right: calc(0px + 43 * ((100vw - 320px) / 1120));
  }
}
.header__logo {
  position: relative;
  z-index: 20;
}
.header__logo img {
  width: 125px;
  height: auto;
}
@media (max-width: 1440px) {
  .header__logo img {
    width: calc(80px + 45 * ((100vw - 320px) / 1120));
  }
}
.header__menu {
  position: relative;
}
.list-header {
  display: flex;
  gap: 24px;
}
@media (max-width: 1440px) {
  .list-header {
    gap: calc(10px + 14 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 767.98px) {
  .list-header {
    position: fixed;
    top: 0;
    left: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background-color: rgba(43, 43, 43, 0.9);
    transition: left 0.3s;
  }
}
.list-header__item {
  cursor: pointer;
  font-size: 37px;
}
@media (max-width: 1440px) {
  .list-header__item {
    font-size: calc(25px + 12 * ((100vw - 320px) / 1120));
  }
}
.list-header__item a {
  color: white;
}

.burger {
  display: none;
}
@media (max-width: 767.98px) {
  .burger {
    display: block;
    position: relative;
    width: 30px;
    z-index: 20;
    height: 21px;
  }
}
@media (max-width: 767.98px) and (max-width: 1440px) {
  .burger {
    height: calc(18px + 3 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 767.98px) {
  .burger span {
    background-color: white;
    display: block;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    opacity: 1;
    transform: rotate(0deg);
    transition: all 0.25s ease-in-out;
  }
  .burger span:nth-child(1) {
    top: 0;
  }
  .burger span:nth-child(2) {
    top: 10px;
  }
  .burger span:nth-child(3) {
    top: 20px;
  }
}
@media (max-width: 767.98px) {
  .burger._active span:nth-child(1) {
    top: 10px;
    transform: rotate(135deg);
  }
  .burger._active span:nth-child(2) {
    opacity: 0;
    left: -30px;
  }
  .burger._active span:nth-child(3) {
    top: 10px;
    transform: rotate(-135deg);
  }
}

