@import "../../styles/variables";
@import "../../styles/mixins";

.form-item {
  display: flex;
  @include adaptive-value('gap', 30, 10, 1);
  @media (max-width: $md3+px) {
    flex-direction: column;
    @include adaptive-value('gap', 10, 10, 1);
  }
  // .form-item__title
  &__title {
    width: 100px;
    height: 47px;
    display: flex;
    justify-content: center;
    align-items: center;
    & span {
    }
  }

  // .form-item__input
  &__input {
    flex: 0 1 330/600*100%;
    text-transform: uppercase;
    padding: 0 5px;
    height: 48px;
    border-radius: 10px;
  }
}