@import "../../styles/variables";
@import "../../styles/mixins";

.contact {
  color: white;
  // .contact__container
  &__container {
    background-color: $grayColor;
  }

  // .contact__body
  &__body {
    padding: 150px 0 50px;
    min-height: 100vh;
    display: flex;
  }

  // .contact__content
  &__content {
  }

  // .contact__decor
  &__decor {
  }
}

.content-contact {
  position: relative;
  flex: 1 1 auto;
  display: flex;
  @include adaptive-value('padding-left', 120, 0, 1);
  @media (max-width: 739.98px) {
    flex-direction: column;
    align-items: center;
    @include adaptive-value('gap', 30, 15, 1);
  }
  // .content-contact__form
  &__form {
    width: 50%;
    padding: 0 40px 0 5px;
  }

  // .content-contact__links
  &__links {
    width: 50%;
    padding: 0 5px;
  }
}

.form-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px white solid;
  max-height: 600px;
  max-width: 500px;
  min-width: 280px;
  min-height: 500px;
  box-shadow: 7px 9px 5px rgba(0, 0, 0, 0.4);
  // .form-contact__body
  &__body {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include adaptive-value('gap', 100, 30, 1);
  }

  // .form-contact__content
  &__content {
    width: 100%;
    display: flex;
    flex-direction: column;
    @include adaptive-value('gap', 48, 20, 1);
  }

  // .form-contact__button
  &__button {
    width: 145px;
    padding: 10px 0;
    border-radius: 50px;
    background-color: black;
    & span {
      color: white;
      text-transform: uppercase;
      @include adaptive-value('font-size', 20, 18, 1);
    }
  }
}

.links-contact {
  position: absolute;
  top: 5%;
  left: 40%;
  @media (max-width: 1268.98px) {
    left: 45%;
  }
  @media (max-width: 899.98px) {
    left: 50%;
  }
  @media (max-width: 739.98px) {
    position: relative;
    left: 0;
    top: 0;
  }
  background-color: $grayColor;
  @include adaptive-value('width', 400, 280, 1);
  @include adaptive-value('height', 500, 380, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 5px;
  box-shadow: 7px 9px 5px rgba(0, 0, 0, 0.4);
  // .links-contact__body
  &__body {
    width: 80%;
  }
}

.decor-contact {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  @media (max-width: $md4+px) {
    display: none;
  }
  // .decor-contact__title
  &__title {
    border-left: 1px solid white;
    padding-left: 5px;
    & span {
      writing-mode: vertical-lr;
    }
  }
}