@import "../../styles/variables";
@import "../../styles/mixins";

.design {
  padding: 100px 0 0;
  background-color: #993737;
  // .design__image
  &__image {
  }

  // .design__container
  &__container {
  }

  // .design__content
  &__content {
  }
}

.image-design {
  position: relative;
  // .image-design__img
  &__img {
    max-width: 1440px;
    & img {
      @include adaptive-value('width', 1440, 320, 1);
      height: auto;
    }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(56, 43, 43, .8);
    }
  }

  // .image-design__body
  &__body {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
  }

  // .image-design__text
  &__text {
    position: relative;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    & span {
      @include adaptive-value('font-size', 150, 50, 1);
      @include adaptive-value('letter-spacing', 50, 5, 1);
    }
  }

  // .image-design__decor
  &__decor {
    display: flex;
    align-items: center;
    padding: 0 5px;
    & span {
      position: relative;
      color: white;
      writing-mode: vertical-lr;
      text-transform: uppercase;
      &:before, &:after {
        content: '';
        position: absolute;
        background-color: white;
        left: 50%;
        width: 1px;
        @include adaptive-value('height', 50, 10, 1);
      }
      &:before {
        top: -70px;
      }
      &:after {
        bottom: -70px;
      }
    }
  }
}

.content-design {
  @include adaptive-value('padding-top', 160, 30, 1);
  padding-bottom: 10px;
  // .content-design__body
  &__body {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
}