@import "../../styles/variables";
@import "../../styles/mixins";

.social-item {
  position: relative;
  display: flex;
  align-items: center;
  // .social-item__img
  &__img {
    & img {
      width: 60px;
      height: 60px;
    }
  }

  // .social-item__decor
  &__decor {
    flex: 0 1 60px;
    position: relative;
    & span {
      position: absolute;
      right: 0;
      background-color: white;
      width: 100%;
      height: 1px;
      @media (max-width: 1099.98px) {
        background-color: transparent;
      }
    }
  }

  // .social-item__link
  &__link {
    position: absolute;
  }
}