.culture-main {
  min-height: 100vh;
  display: flex;
  align-items: center;
}
@media (min-height: 899.98px) {
  .culture-main {
    min-height: 900px;
  }
}
@media (max-height: 844.98px) {
  .culture-main {
    min-height: 850px;
  }
}
.culture-main__body {
  margin: 0 auto;
  display: flex;
  align-items: center;
  max-width: 1200px;
  justify-content: space-between;
  min-height: 630px;
  gap: 150px;
}
@media (max-width: 1440px) {
  .culture-main__body {
    gap: calc(40px + 110 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 767.98px) {
  .culture-main__body {
    padding: 10px 0;
    flex-direction: column;
  }
}
.culture-main__video img {
  transform: scale(0.9);
  width: 500px;
  height: auto;
}
@media (max-width: 1440px) {
  .culture-main__video img {
    width: calc(250px + 250 * ((100vw - 320px) / 1120));
  }
}

.info-culture {
  max-width: 480px;
  display: flex;
  flex-direction: column;
  gap: 50px;
}
@media (max-width: 1440px) {
  .info-culture {
    gap: calc(20px + 30 * ((100vw - 320px) / 1120));
  }
}
.info-culture__title {
  font-size: 50px;
}
@media (max-width: 1440px) {
  .info-culture__title {
    font-size: calc(30px + 20 * ((100vw - 320px) / 1120));
  }
}
.info-culture__text {
  font-size: 24px;
}
@media (max-width: 1440px) {
  .info-culture__text {
    font-size: calc(16px + 8 * ((100vw - 320px) / 1120));
  }
}
.info-culture__img img {
  width: 300px;
  height: auto;
}
@media (max-width: 1440px) {
  .info-culture__img img {
    width: calc(100px + 200 * ((100vw - 320px) / 1120));
  }
}

