.social-item {
  position: relative;
  display: flex;
  align-items: center;
}
.social-item__img img {
  width: 60px;
  height: 60px;
}
.social-item__decor {
  flex: 0 1 60px;
  position: relative;
}
.social-item__decor span {
  position: absolute;
  right: 0;
  background-color: white;
  width: 100%;
  height: 1px;
}
@media (max-width: 1099.98px) {
  .social-item__decor span {
    background-color: transparent;
  }
}
.social-item__link {
  position: absolute;
}

