@import "../../styles/mixins";
@import "../../styles/variables";

.about {
  position: relative;
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 110px 0 10px;
  @media (max-width: 828.98px) {
    flex-direction: column;
    @include adaptive-value('gap', 40, 20, 1);
  }
  // .about__container
  &__container {
    margin: 0;
    display: flex;
    justify-content: flex-start;
  }

  // .about__body
  &__body {
    display: flex;
    flex-direction: column;
    @include adaptive-value('gap', 30, 10, 1);
    //@include adaptive-value('margin-left', 100, 0, 1);
  }

  // .about__info
  &__info {
  }

  // .about__button
  &__button {
  }

  // .about__img
  &__img {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    & img {
      @include adaptive-value('width', 500, 280, 1);
      height: auto;
    }
  }
}

.info-about {
  max-width: 660px;
  display: flex;
  flex-direction: column;
  @include adaptive-value('gap', 80, 40, 1);
  // .info-about__title
  &__title {
    font-weight: 700;
    @include adaptive-value('font-size', 70, 45, 1);
  }

  // .info-about__sub-body
  &__sub-body {
    display: flex;
    flex-direction: column;
    @include adaptive-value('gap', 25, 15, 1);
    @include adaptive-value('width', 550, 280, 1);
  }

  // .info-about__sub-title
  &__sub-title {
    @include adaptive-value('font-size', 30, 20, 1);
  }

  // .info-about__text
  &__text {
    @include adaptive-value('font-size', 25, 16, 1);
  }
}

.button-about {
  max-width: 660px;
  display: flex;
  @include adaptive-value('gap', 30, 10, 1);
  @media (max-width: 374.98px) {
    flex-direction: column;
  }
  // .button-about__item
  &__item {
    @include adaptive-value('width', 250, 150, 1);
    padding: 10px 0;
    border-radius: 50px;
    &:nth-child(1) {
      border: 2px solid black;
      background-color: black;
      & a {
        color: white;
      }
    }
    &:nth-child(2) {
      border: 2px solid black;
      background-color: white;
      & a {
        color: black;
      }
    }
    & a {
      @include adaptive-value('font-size', 40, 20, 1);
    }
  }
}