@import "../../../styles/variables";
@import "../../../styles/mixins";

.hero-main {
  min-height: 100vh;
  display: flex;
  padding: 10px 0;
  @media (min-height: 899.98px) {
    min-height: 900px;
  }
  @media (max-height: 844.98px) {
    min-height: 850px;
  }
  // .hero-main__body
  &__body {
    flex: 1 1 auto;
    width: 100%;
    position: relative;
  }

  // .hero-main__img
  &__img {
    min-height: 100vh;
    position: absolute;
    display: flex;
    flex-direction: column;
    left: 0;
    @media (min-height: 899.98px) {
      min-height: 900px;
    }
    @media (max-height: 844.98px) {
      min-height: 850px;
    }
    & img:nth-child(1){
      position: absolute;
      @include adaptive-value('top', 250, 200, 1);
      @include adaptive-value('width', 1000, 300, 1);
      height: auto;
    }
    & img:nth-child(2){
      position: absolute;
      bottom: -70px;
      @include adaptive-value('margin-left', 30, 0, 1);
      @include adaptive-value('width', 195, 130, 1);
      height: auto;
    }
  }

  // .hero-main__container
  &__container {
    display: flex;
  }

  // .hero-main__content
  &__content {
  }

  // .hero-main__decor
  &__decor {
  }
}

.content-hero {
  display: flex;
  flex: 1 1 auto;
  justify-content: space-between;
  @media (max-width: 644.98px) {
    flex-direction: column;
    justify-content: center;
  }
  // .content-hero__img
  &__img {
    position: relative;
    left: 0;
    top: 90px;
    overflow: hidden;
    //@include adaptive-value('top', 90, 90, 1);
    //@media (max-width: $md4+px) {
    //  display: none;
    //}
    @media (max-height: 679.98px) {
      left: -3%;
    }
    & img{
      position: relative;
      @include adaptive-value('left', 85, 0, 1);
      @include adaptive-value('width', 500, 240, 1);
      //@include adaptive-value('height', 650, 390, 1);
      height: auto;
      transform: scale(1.2);
      @media (max-height: 679.98px) {
        transform: scale(1.0);
      }
    }
  }

  // .content-hero__body
  &__body {
    @include adaptive-value('padding-top', 420, 150, 1);
    @include adaptive-value('padding-right', 140, 10, 1);
    display: flex;
    flex-direction: column;
    @include adaptive-value('gap', 100, 50, 1);
    @media (max-width: $md4+px) {
      padding-right: 0;
    }
  }

  // .content-hero__info
  &__info {
    display: flex;
    flex-direction: column;
    @include adaptive-value('gap', 21, 15, 1);
  }

  // .content-hero__title
  &__title {
    text-align: center;
    text-decoration: line-through;
    letter-spacing: 22/45*1px;
    @include adaptive-value('font-size', 45, 30, 1);
  }

  // .content-hero__text
  &__text {
    max-width: 340px;
    line-height: 144%;
    @include adaptive-value('font-size', 20, 16, 1);
  }

  // .content-hero__button
  &__button {
    max-width: 360px;
    border-radius: 50px;
    background-color: black;
    color: white;
    padding: 10px 0;
    & span {
      @include adaptive-value('font-size', 40, 25, 1);
    }
  }
}

.decor-hero {
  position: absolute;
  @include adaptive-value('top', 250, 200, 1);
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-right: 20px;
  @include adaptive-value('gap', 400, 100, 1);
  @media (max-width: $md3+px) {
    display: none;
  }
  // .decor-hero__body
  &__body {
    position: relative;
    display: flex;
    &:before, &:after {
      content: "";
      position: absolute;
      background-color: black;
      width: 1px;
      height: 50px;
      left: 50%;
    }
    &:before {
      top: -150%;
    }
    &:after {
      bottom: -150%;
    }
  }

  // .decor-hero__title
  &__title {
    writing-mode: vertical-lr;
    text-transform: uppercase;
    cursor: default;
    @include adaptive-value('font-size', 20, 16, 1);
  }

  // .decor-hero__arrow
  &__arrow {
  }
}