.design {
  padding: 100px 0 0;
  background-color: #993737;
}
.image-design {
  position: relative;
}
.image-design__img {
  max-width: 1440px;
}
.image-design__img img {
  width: 1440px;
  height: auto;
}
@media (max-width: 1440px) {
  .image-design__img img {
    width: calc(320px + 1120 * ((100vw - 320px) / 1120));
  }
}
.image-design__img:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(56, 43, 43, 0.8);
}
.image-design__body {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
}
.image-design__text {
  position: relative;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.image-design__text span {
  font-size: 150px;
  letter-spacing: 50px;
}
@media (max-width: 1440px) {
  .image-design__text span {
    font-size: calc(50px + 100 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .image-design__text span {
    letter-spacing: calc(5px + 45 * ((100vw - 320px) / 1120));
  }
}
.image-design__decor {
  display: flex;
  align-items: center;
  padding: 0 5px;
}
.image-design__decor span {
  position: relative;
  color: white;
  writing-mode: vertical-lr;
  text-transform: uppercase;
}
.image-design__decor span:before, .image-design__decor span:after {
  content: "";
  position: absolute;
  background-color: white;
  left: 50%;
  width: 1px;
  height: 50px;
}
@media (max-width: 1440px) {
  .image-design__decor span:before, .image-design__decor span:after {
    height: calc(10px + 40 * ((100vw - 320px) / 1120));
  }
}
.image-design__decor span:before {
  top: -70px;
}
.image-design__decor span:after {
  bottom: -70px;
}

.content-design {
  padding-top: 160px;
  padding-bottom: 10px;
}
@media (max-width: 1440px) {
  .content-design {
    padding-top: calc(30px + 130 * ((100vw - 320px) / 1120));
  }
}
.content-design__body {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

