.design-main {
  background-color: #2B2B2B;
  min-height: 100vh;
  color: white;
  display: flex;
  align-items: center;
}
@media (min-height: 899.98px) {
  .design-main {
    min-height: 900px;
  }
}
@media (max-height: 844.98px) {
  .design-main {
    min-height: 850px;
  }
}
.design-main__body {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 630px;
  max-width: 1200px;
  gap: 100px;
}
@media (max-width: 1440px) {
  .design-main__body {
    gap: calc(40px + 60 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 767.98px) {
  .design-main__body {
    flex-direction: column;
    justify-content: center;
    padding: 10px 0;
  }
}
.design-main__img img {
  width: 600px;
  height: auto;
}
@media (max-width: 1440px) {
  .design-main__img img {
    width: calc(250px + 350 * ((100vw - 320px) / 1120));
  }
}

.info-design {
  max-width: 490px;
  display: flex;
  flex-direction: column;
  gap: 76px;
}
@media (max-width: 1440px) {
  .info-design {
    gap: calc(30px + 46 * ((100vw - 320px) / 1120));
  }
}
.info-design__content {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
@media (max-width: 1440px) {
  .info-design__content {
    gap: calc(20px + 20 * ((100vw - 320px) / 1120));
  }
}
.info-design__title {
  text-transform: uppercase;
  font-size: 55px;
}
@media (max-width: 1440px) {
  .info-design__title {
    font-size: calc(30px + 25 * ((100vw - 320px) / 1120));
  }
}
.info-design__text {
  font-size: 25px;
  line-height: 144%;
}
@media (max-width: 1440px) {
  .info-design__text {
    font-size: calc(16px + 9 * ((100vw - 320px) / 1120));
  }
}
.info-design__button {
  max-width: 390px;
  border-radius: 50px;
  padding: 10px 0;
}
.info-design__button a {
  color: black;
  text-transform: uppercase;
  font-size: 40px;
}
@media (max-width: 1440px) {
  .info-design__button a {
    font-size: calc(25px + 15 * ((100vw - 320px) / 1120));
  }
}

