.hero-main {
  min-height: 100vh;
  display: flex;
  padding: 10px 0;
}
@media (min-height: 899.98px) {
  .hero-main {
    min-height: 900px;
  }
}
@media (max-height: 844.98px) {
  .hero-main {
    min-height: 850px;
  }
}
.hero-main__body {
  flex: 1 1 auto;
  width: 100%;
  position: relative;
}
.hero-main__img {
  min-height: 100vh;
  position: absolute;
  display: flex;
  flex-direction: column;
  left: 0;
}
@media (min-height: 899.98px) {
  .hero-main__img {
    min-height: 900px;
  }
}
@media (max-height: 844.98px) {
  .hero-main__img {
    min-height: 850px;
  }
}
.hero-main__img img:nth-child(1) {
  position: absolute;
  top: 250px;
  width: 1000px;
  height: auto;
}
@media (max-width: 1440px) {
  .hero-main__img img:nth-child(1) {
    top: calc(200px + 50 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .hero-main__img img:nth-child(1) {
    width: calc(300px + 700 * ((100vw - 320px) / 1120));
  }
}
.hero-main__img img:nth-child(2) {
  position: absolute;
  bottom: -70px;
  margin-left: 30px;
  width: 195px;
  height: auto;
}
@media (max-width: 1440px) {
  .hero-main__img img:nth-child(2) {
    margin-left: calc(0px + 30 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .hero-main__img img:nth-child(2) {
    width: calc(130px + 65 * ((100vw - 320px) / 1120));
  }
}
.hero-main__container {
  display: flex;
}
.content-hero {
  display: flex;
  flex: 1 1 auto;
  justify-content: space-between;
}
@media (max-width: 644.98px) {
  .content-hero {
    flex-direction: column;
    justify-content: center;
  }
}
.content-hero__img {
  position: relative;
  left: 0;
  top: 90px;
  overflow: hidden;
}
@media (max-height: 679.98px) {
  .content-hero__img {
    left: -3%;
  }
}
.content-hero__img img {
  position: relative;
  left: 85px;
  width: 500px;
  height: auto;
  transform: scale(1.2);
}
@media (max-width: 1440px) {
  .content-hero__img img {
    left: calc(0px + 85 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .content-hero__img img {
    width: calc(240px + 260 * ((100vw - 320px) / 1120));
  }
}
@media (max-height: 679.98px) {
  .content-hero__img img {
    transform: scale(1);
  }
}
.content-hero__body {
  padding-top: 420px;
  padding-right: 140px;
  display: flex;
  flex-direction: column;
  gap: 100px;
}
@media (max-width: 1440px) {
  .content-hero__body {
    padding-top: calc(150px + 270 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .content-hero__body {
    padding-right: calc(10px + 130 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .content-hero__body {
    gap: calc(50px + 50 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 479.98px) {
  .content-hero__body {
    padding-right: 0;
  }
}
.content-hero__info {
  display: flex;
  flex-direction: column;
  gap: 21px;
}
@media (max-width: 1440px) {
  .content-hero__info {
    gap: calc(15px + 6 * ((100vw - 320px) / 1120));
  }
}
.content-hero__title {
  text-align: center;
  text-decoration: line-through;
  letter-spacing: 0.4888888889px;
  font-size: 45px;
}
@media (max-width: 1440px) {
  .content-hero__title {
    font-size: calc(30px + 15 * ((100vw - 320px) / 1120));
  }
}
.content-hero__text {
  max-width: 340px;
  line-height: 144%;
  font-size: 20px;
}
@media (max-width: 1440px) {
  .content-hero__text {
    font-size: calc(16px + 4 * ((100vw - 320px) / 1120));
  }
}
.content-hero__button {
  max-width: 360px;
  border-radius: 50px;
  background-color: black;
  color: white;
  padding: 10px 0;
}
.content-hero__button span {
  font-size: 40px;
}
@media (max-width: 1440px) {
  .content-hero__button span {
    font-size: calc(25px + 15 * ((100vw - 320px) / 1120));
  }
}

.decor-hero {
  position: absolute;
  top: 250px;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-right: 20px;
  gap: 400px;
}
@media (max-width: 1440px) {
  .decor-hero {
    top: calc(200px + 50 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .decor-hero {
    gap: calc(100px + 300 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 767.98px) {
  .decor-hero {
    display: none;
  }
}
.decor-hero__body {
  position: relative;
  display: flex;
}
.decor-hero__body:before, .decor-hero__body:after {
  content: "";
  position: absolute;
  background-color: black;
  width: 1px;
  height: 50px;
  left: 50%;
}
.decor-hero__body:before {
  top: -150%;
}
.decor-hero__body:after {
  bottom: -150%;
}
.decor-hero__title {
  writing-mode: vertical-lr;
  text-transform: uppercase;
  cursor: default;
  font-size: 20px;
}
@media (max-width: 1440px) {
  .decor-hero__title {
    font-size: calc(16px + 4 * ((100vw - 320px) / 1120));
  }
}

