.about {
  position: relative;
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 110px 0 10px;
}
@media (max-width: 828.98px) {
  .about {
    flex-direction: column;
    gap: 40px;
  }
}
@media (max-width: 828.98px) and (max-width: 1440px) {
  .about {
    gap: calc(20px + 20 * ((100vw - 320px) / 1120));
  }
}
.about__container {
  margin: 0;
  display: flex;
  justify-content: flex-start;
}
.about__body {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (max-width: 1440px) {
  .about__body {
    gap: calc(10px + 20 * ((100vw - 320px) / 1120));
  }
}
.about__img {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.about__img img {
  width: 500px;
  height: auto;
}
@media (max-width: 1440px) {
  .about__img img {
    width: calc(280px + 220 * ((100vw - 320px) / 1120));
  }
}

.info-about {
  max-width: 660px;
  display: flex;
  flex-direction: column;
  gap: 80px;
}
@media (max-width: 1440px) {
  .info-about {
    gap: calc(40px + 40 * ((100vw - 320px) / 1120));
  }
}
.info-about__title {
  font-weight: 700;
  font-size: 70px;
}
@media (max-width: 1440px) {
  .info-about__title {
    font-size: calc(45px + 25 * ((100vw - 320px) / 1120));
  }
}
.info-about__sub-body {
  display: flex;
  flex-direction: column;
  gap: 25px;
  width: 550px;
}
@media (max-width: 1440px) {
  .info-about__sub-body {
    gap: calc(15px + 10 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .info-about__sub-body {
    width: calc(280px + 270 * ((100vw - 320px) / 1120));
  }
}
.info-about__sub-title {
  font-size: 30px;
}
@media (max-width: 1440px) {
  .info-about__sub-title {
    font-size: calc(20px + 10 * ((100vw - 320px) / 1120));
  }
}
.info-about__text {
  font-size: 25px;
}
@media (max-width: 1440px) {
  .info-about__text {
    font-size: calc(16px + 9 * ((100vw - 320px) / 1120));
  }
}

.button-about {
  max-width: 660px;
  display: flex;
  gap: 30px;
}
@media (max-width: 1440px) {
  .button-about {
    gap: calc(10px + 20 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 374.98px) {
  .button-about {
    flex-direction: column;
  }
}
.button-about__item {
  width: 250px;
  padding: 10px 0;
  border-radius: 50px;
}
@media (max-width: 1440px) {
  .button-about__item {
    width: calc(150px + 100 * ((100vw - 320px) / 1120));
  }
}
.button-about__item:nth-child(1) {
  border: 2px solid black;
  background-color: black;
}
.button-about__item:nth-child(1) a {
  color: white;
}
.button-about__item:nth-child(2) {
  border: 2px solid black;
  background-color: white;
}
.button-about__item:nth-child(2) a {
  color: black;
}
.button-about__item a {
  font-size: 40px;
}
@media (max-width: 1440px) {
  .button-about__item a {
    font-size: calc(20px + 20 * ((100vw - 320px) / 1120));
  }
}

