@import "nullstyle";
//@import "fonts";
//@import "icons";
@import "variables";
@import "mixins";


body {
  color: $mainColor;
  font-family: $fontFamily;
  font-size: $fontSize+px;
}

[class*="_container"] {
  max-width: $maxWidthContainer+px;
  padding: 0 15px;
  margin: 0 auto;
}

.content {
  flex: 1 1 auto;
}

._hover-red {
  transition: color .3s;
  &:hover {
    color: $redColor;
  }
}

._accordion {
  transition: all .2s;
  &:hover{
    letter-spacing: 2px;
  }
}

.arrow {
}

