@import "../../styles/variables";
@import "../../styles/mixins";

.footer {
  background-color: $grayColor;
  color: white;
  max-width: $maxWidth+px;
  width: 100%;
  margin: 0 auto;
  // .footer__container
  &__container {
  }

  // .footer__body
  &__body {
    @include adaptive-value('min-height', 388, 250, 1);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    @include adaptive-value('padding-left', 107, 0, 1);
    @include adaptive-value('padding-right', 107, 0, 1);
  }

  // .footer__communication
  &__communication {
  }

  // .footer__rights
  &__rights {
  }
}

.communication-footer {
  display: flex;
  padding: 5px;
  @include adaptive-value('min-height', 300, 162, 1);
  @media (max-width: $md3+px) {
    flex-direction: column;
    @include adaptive-value('gap', 30, 20, 1);
  }
  // .communication-footer__block
  &__block {
  }
}

.contact-footer {
  flex: 1 1 550/$maxWidthContainer*100%;
  border-right: white 1px solid;
  display: flex;
  align-items: center;
  @media (max-width: $md3+px) {
    border: 0;
    justify-content: center;
  }

  // .contact-footer__list
  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include adaptive-value('gap', 33, 15, 1);
  }

  // .contact-footer__item
  &__item {
    min-width: 250px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  // .contact-footer__img
  &__img {
    width: 50px;
    height: 50px;
  }

  // .contact-footer__text
  &__text {
    @include adaptive-value('font-size', 24, 20, 1);
  }
}

.social-network {
  flex: 1 1 550/$maxWidthContainer*100%;
  border-left: white 1px solid;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  @include adaptive-value('gap', 2, 0, 1);
  @media (max-width: $md3+px) {
    border: 0;
    justify-content: center;
  }
  // .social-network__img
  &__img {
    width: 60px;
    height: 60px;
  }
}

.rights-footer {
  margin: 0 auto;
  max-width: 300px;
  display: flex;
  @include adaptive-value('gap', 13, 10, 1);
  align-items: center;
  padding-bottom: 5px;
  // .rights-footer__img
  &__img {
    width: 20px;
    height: 20px;
  }

  // .rights-footer__text
  &__text {
    @include adaptive-value('font-size', 16, 12, 1)
  }
}