@import "../../../styles/variables";
@import "../../../styles/mixins";

.main {
  // .main__design
  &__design {
  }
}

.design-main {
  background-color: $grayColor;
  min-height: 100vh;
  color: white;
  display: flex;
  align-items: center;
  @media (min-height: 899.98px) {
    min-height: 900px;
  }
  @media (max-height: 844.98px) {
    min-height: 850px;
  }
  // .design-main__container
  &__container {
  }

  // .design-main__body
  &__body {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 630px;
    max-width: 1200px;
    @include adaptive-value('gap', 100, 40, 1);
    @media (max-width: $md3+px) {
      flex-direction: column;
      justify-content: center;
      padding: 10px 0;
    }
  }

  // .design-main__info
  &__info {
  }

  // .design-main__img
  &__img {
    //@include adaptive-value('padding-top', 100, 60, 1);
    & img {
      @include adaptive-value('width', 600, 250, 1);
      height: auto;
    }
  }
}

.info-design {
  max-width: 490px;
  display: flex;
  flex-direction: column;
  @include adaptive-value('gap', 76, 30, 1);
  //@include adaptive-value('padding-top', 200, 100, 1);
  // .info-design__content
  &__content {
    display: flex;
    flex-direction: column;
    @include adaptive-value('gap', 40, 20, 1);
  }

  // .info-design__title
  &__title {
    text-transform: uppercase;
    @include adaptive-value('font-size', 55, 30, 1);
  }

  // .info-design__text
  &__text {
    @include adaptive-value('font-size', 25, 16, 1);
    line-height: 144%;
  }

  // .info-design__button
  &__button {
    max-width: 390px;
    border-radius: 50px;
    padding: 10px 0;
    & a {
      color: black;
      text-transform: uppercase;
      @include adaptive-value('font-size', 40, 25, 1);
    }
  }
}