.card {
  padding: 10px 10px;
}
.card__body {
  display: flex;
  flex-direction: column;
  gap: 90px;
}
@media (max-width: 1440px) {
  .card__body {
    gap: calc(30px + 60 * ((100vw - 320px) / 1120));
  }
}
.card__title {
  text-align: center;
}
.card__title span {
  text-transform: uppercase;
  font-size: 30px;
}
@media (max-width: 1440px) {
  .card__title span {
    font-size: calc(20px + 10 * ((100vw - 320px) / 1120));
  }
}
.card__img img {
  width: 330px;
}
@media (max-width: 1440px) {
  .card__img img {
    width: calc(290px + 40 * ((100vw - 320px) / 1120));
  }
}

