.contact {
  color: white;
}
.contact__container {
  background-color: #2B2B2B;
}
.contact__body {
  padding: 150px 0 50px;
  min-height: 100vh;
  display: flex;
}
.content-contact {
  position: relative;
  flex: 1 1 auto;
  display: flex;
  padding-left: 120px;
}
@media (max-width: 1440px) {
  .content-contact {
    padding-left: calc(0px + 120 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 739.98px) {
  .content-contact {
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }
}
@media (max-width: 739.98px) and (max-width: 1440px) {
  .content-contact {
    gap: calc(15px + 15 * ((100vw - 320px) / 1120));
  }
}
.content-contact__form {
  width: 50%;
  padding: 0 40px 0 5px;
}
.content-contact__links {
  width: 50%;
  padding: 0 5px;
}

.form-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px white solid;
  max-height: 600px;
  max-width: 500px;
  min-width: 280px;
  min-height: 500px;
  box-shadow: 7px 9px 5px rgba(0, 0, 0, 0.4);
}
.form-contact__body {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 100px;
}
@media (max-width: 1440px) {
  .form-contact__body {
    gap: calc(30px + 70 * ((100vw - 320px) / 1120));
  }
}
.form-contact__content {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 48px;
}
@media (max-width: 1440px) {
  .form-contact__content {
    gap: calc(20px + 28 * ((100vw - 320px) / 1120));
  }
}
.form-contact__button {
  width: 145px;
  padding: 10px 0;
  border-radius: 50px;
  background-color: black;
}
.form-contact__button span {
  color: white;
  text-transform: uppercase;
  font-size: 20px;
}
@media (max-width: 1440px) {
  .form-contact__button span {
    font-size: calc(18px + 2 * ((100vw - 320px) / 1120));
  }
}

.links-contact {
  position: absolute;
  top: 5%;
  left: 40%;
  background-color: #2B2B2B;
  width: 400px;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 5px;
  box-shadow: 7px 9px 5px rgba(0, 0, 0, 0.4);
}
@media (max-width: 1268.98px) {
  .links-contact {
    left: 45%;
  }
}
@media (max-width: 899.98px) {
  .links-contact {
    left: 50%;
  }
}
@media (max-width: 739.98px) {
  .links-contact {
    position: relative;
    left: 0;
    top: 0;
  }
}
@media (max-width: 1440px) {
  .links-contact {
    width: calc(280px + 120 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .links-contact {
    height: calc(380px + 120 * ((100vw - 320px) / 1120));
  }
}
.links-contact__body {
  width: 80%;
}

.decor-contact {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
@media (max-width: 479.98px) {
  .decor-contact {
    display: none;
  }
}
.decor-contact__title {
  border-left: 1px solid white;
  padding-left: 5px;
}
.decor-contact__title span {
  writing-mode: vertical-lr;
}

