@import "../../styles/variables";
@import "../../styles/mixins";

.header {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  margin: 0 auto;
  max-width: $maxWidth+px;
  color: white;
  background-color: $grayColor;
  box-shadow: 0 7px 5px rgba(0, 0, 0, 0.2);
  // .header__container
  &__container {
    display: flex;
    align-items: center;
  }

  // .header__body
  &__body {
    min-height: 100px;
    @include adaptive-value('padding-left', 43, 0, 1);
    @include adaptive-value('padding-right', 43, 0, 1);
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 1 1 1324/$maxWidthContainer*100%;
  }

  // .header__logo
  &__logo {
    position: relative;
    z-index: 20;
    & img {
      @include adaptive-value('width', 125, 80, 1);
      height: auto;
    }
  }

  // .header__menu
  &__menu {
    position: relative;
  }

  // .header__list
  &__list {
  }

  // .header__burger
  &__burger {
  }
}

.list-header {
  display: flex;
  @include adaptive-value("gap", 24, 10, 1);
  // .list-header__item
  @media (max-width: $md3+px) {
    position: fixed;
    top: 0;
    left: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background-color: rgba(43, 43, 43, .9);
    transition: left .3s;
  }
  &__item {
    cursor: pointer;
    @include adaptive-value("font-size", 37, 25, 1);
    & a {
      color: white;
    }
  }
}

.burger {
  display: none;
  @media (max-width: $md3+px) {
    display: block;
    position: relative;
    width: 30px;
    z-index: 20;
    @include adaptive-value('height', 21, 18, 1);
    & span{
      background-color: white;
      display: block;
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      opacity: 1;
      transform: rotate(0deg);
      transition: all .25s ease-in-out;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: 10px;
      }
      &:nth-child(3){
        top: 20px;
      }
    }
    &._active {
      & span{
        &:nth-child(1) {
          top: 10px;
          transform: rotate(135deg);
        }
        &:nth-child(2) {
          opacity: 0;
          left: -30px;
        }
        &:nth-child(3){
          top: 10px;
          transform: rotate(-135deg);
        }
      }
    }
  }
}
