@import "../../../styles/variables";
@import "../../../styles/mixins";

.main {
  // .main__culture
  &__culture {
  }
}

.culture-main {
  min-height: 100vh;
  display: flex;
  align-items: center;
  @media (min-height: 899.98px) {
    min-height: 900px;
  }
  @media (max-height: 844.98px) {
    min-height: 850px;
  }
  // .culture-main__container
  &__container {
  }

  // .culture-main__body
  &__body {
    margin: 0 auto;
    display: flex;
    align-items: center;
    max-width: 1200px;
    justify-content: space-between;
    min-height: 630px;
    @include adaptive-value('gap', 150, 40, 1);
    @media (max-width: $md3+px) {
      padding: 10px 0;
      flex-direction: column;
    }
  }

  // .culture-main__info
  &__info {
  }

  // .culture-main__video
  &__video {
    & img {
      transform: scale(0.9);
      @include adaptive-value('width', 500, 250, 1);
      height: auto;
    }
  }
}

.info-culture {
  max-width: 480px;
  display: flex;
  flex-direction: column;
  @include adaptive-value('gap', 50, 20, 1);
  // .info-culture__title
  &__title {
    @include adaptive-value('font-size', 50, 30, 1);
  }

  // .info-culture__text
  &__text {
    @include adaptive-value('font-size', 24, 16, 1);
  }

  // .info-culture__img
  &__img {
    & img {
      @include adaptive-value('width', 300, 100, 1);
      height: auto;
    }
  }
}